import { constantRoutes } from '@/router'
import Layout from '@/layout'

let ZHLayout = "" // layout
// import ZHLayout from "@/views/submodule/zhonghui/components/layout.vue"

const permission = {
  state: {
    routers: [],
    hasDashboard: true
  },
  mutations: {
    setRoutes: (state, routers) => {
      state.routers = constantRoutes.concat(routers)
    },
    HAS_DASHBOARD: (state, hasDashboard) => {
      state.hasDashboard = hasDashboard
    }
  },
}

// 
export const filterAsyncRouter = (routers) => {
  const accessedRouters = routers.filter(router => {
    if (router.component) {
      // Layout
      if (router.component === 'ZHLayout') {
        if (!ZHLayout) {
          try {
            // importrequire
            ZHLayout = require("../../views/submodule/zhonghui/components/layout.vue").default
          } catch (error) {
            console.log(error);
          }
        }
        router.component = ZHLayout || Layout // wcslayout
      } else if (router.component === 'Layout') {
        router.component = Layout // layout
      } else {
        const component = router.component
        router.component = loadView(component)
      }
    }
    if (router.children && router.children.length) {
      router.children = filterAsyncRouter(router.children)
    }
    return true
  })
  return accessedRouters
}

const submoduleList = ["wcs", "wms"]  //

// 
export const loadView = (view) => {
  //  submodule/
  let prefix = view.split("/")[0]
  if (submoduleList.indexOf(prefix) > -1) view = "submodule/" + view
  return (resolve) => require([`@/views/${view}`], resolve)
}

export default permission
