import axios from 'axios'
import { Message } from "element-ui";
import { getTokenCopy, removeAll } from '@/utils/auth'
import store from '@/store'
const defaultSettings = window.globalConfig
import qs from "qs"
import { handleLargeScreenHash } from "@/utils/monitorUtils.js"
const downloadUrlList = ["amr/rcs/log/download"]  //get
const timeout = 10 * 60 * 1000 //
const oldRcsUrlList = ["dispatch/vehicle/getActiveVehicles"] //1.0
const timeoutUrlList = ["handleProgram", "/editor/saveServereMapZip", "/map/getMapDetailById"]  // 

const instance = axios.create({
  baseURL: process.env.VUE_APP_BASE_API,
  timeout: 10000,
  // headers: {
  //   "name": defaultSettings.name, "token": defaultSettings.token
  // }
});

export const request = {
  async get(url, data, responseType = "", contentType = "application/json;charset=UTF-8") {
    try {
      if (downloadUrlList.indexOf(url) > -1) return handleGetDownload(url, data)
      let params = {
        method: "get", url, params: data, responseType, headers: {
          "Content-Type": contentType
        }
      };
      let { data: res } = await instance(handleParams(params))
      return handleResData.success(res)
    } catch (err) {
      handleResData.error(err)
    }
  },
  async post(url, data, responseType = "", contentType = "application/json;charset=UTF-8") {
    try {
      let params = {
        method: "post", url, data: data || {}, responseType, headers: {
          "Content-Type": contentType
        }
      };
      let { data: res } = await instance(handleParams(params));
      return handleResData.success(res)
    } catch (err) {
      handleResData.error(err)
    }
  },
  async delete(url, data) {
    try {
      let params = {
        method: "delete", url, params: data
      };
      let { data: res } = await instance(handleParams(params));
      return handleResData.success(res)
    } catch (err) {
      handleResData.error(err)
    }
  },
  async patch(url, data) {
    try {
      let params = {
        method: "patch", url, params: data
      };
      let { data: res } = await instance(handleParams(params));
      return handleResData.success(res)
    } catch (err) {
      handleResData.error(err)
    }
  },
  async put(url, data) {
    try {
      let params = {
        method: "put", url, data: data || {}
      };
      let { data: res } = await instance(handleParams(params));
      return handleResData.success(res)
    } catch (err) {
      handleResData.error(err)
    }
  }
};

// 
const handleParams = (params => {
  // 
  if (params.responseType === 'blob' || timeoutUrlList.includes(params.url) || params.url.includes("map/uploadFile")) {
    params.timeout = timeout
  }
  //  1.0  
  if (oldRcsUrlList.indexOf(params.url) > -1 || (!getTokenCopy() && handleLargeScreenHash())) {
    params.headers.name = defaultSettings.name
    params.headers.token = defaultSettings.token
  }
  return params
})

// 
const handleResData = {
  success: (res) => {
    if (!res) {
      Message.error(", ")
    } else {
      let msg = res.errMsg || res.message;
      if (res && res.state === false) {
        // 
        if (msg !== '') Message.error(msg)
      }
      if (res && res.code !== undefined && res.code !== 0) Message.error(msg)
    }
    return res
  },
  error: (err) => {
    if (err.response.status === 401) {
      // token
      if (getTokenCopy()) {
        // 
        if (handleLargeScreenHash()) {
          localStorage.setItem("historyPage", "/largeScreen")
        } else {
          localStorage.removeItem("historyPage")
        }
        removeAll()
        Message.error('')
        store.dispatch('LogOut')
      }
    } else {
      Message.error("Error" + err)
    }
  }
}

// (get)
const handleGetDownload = (url, data) => {
  let a = document.createElement("A");
  a.href = "api/" + url + "?" + qs.stringify(data);
  a.style.display = "none"
  document.body.append(a)
  a.click()
  a.remove()
}

