import {
    amrOldStatusEnum,
    amrStateList,
} from "../../public/static/Enumeration";
import * as _ from "lodash";

// 1.0
const jsonData = require("../../public/static/json/warning.json")
const warnData = jsonData.Warning
export const errorStatus = (warnId) => {
    for (const i of warnData) {
        if (i.id == warnId) {
            return i.name
        }
    }

}

// 1.0
export const handleOldAmrData = (oldAmrData) => {
    let oldAmrList = []
    if (Array.isArray(oldAmrData)) {
        oldAmrData.forEach(({ po, dto }) => {
            let params = {
                name: po["code"] + " (v1.0)",
                id: -Number(po["id"]),
                coordinate: {
                    x: _.round(dto["x"], 3),
                    y: _.round(dto["y"], 3),
                    theta: _.round(dto["dir"], 2),
                },
                stateId: amrOldStatusEnum(dto["status"]),
                state: amrStateList[amrOldStatusEnum(dto["status"])],
                loadStatus: dto["carryState"] !== 0 ? "load" : "EMPTY",
                batteryPercentile: dto["ele"],
                unFinishRoute: { unfinishedVertexes: [] },
                amrException: { exception: [] },
            };
            //  
            if (dto["targetName"]) {
                params.unFinishRoute.unfinishedVertexes = [
                    { code: dto["targetName"] },
                ];
            }
            // 
            if (dto["warnId"]) {
                params.amrException.exception = [errorStatus(dto["warnId"])];
            }
            oldAmrList.push(params);
        });
    }
    return oldAmrList
}

// id
export const encodeAreaId = (id) => {
    id = (id * 199204 + 666) * 202201 - 888
    return id
}

// id
export const decodeAreaId = (id) => {
    if (!id) return 0
    id = ((Number(id) + 888) / 202201 - 666) / 199204
    return id
}

// 
export const handleLargeScreenHash = () => {
    let hash = window.location.hash
    return hash.split("?")[0] === "#/largeScreen"
}

let idList = [] // id

// 
export const sortAmrList = (amrList, oldAmrList, isSortAmr) => {
    let result = (amrList || []).concat(oldAmrList);
    let dangerList = []; // 
    let warningList = []; // 
    let normalList = []; // 
    //1.0
    result.forEach((item) => {
        if (item["stateId"] === 7) item["stateId"] = 8; // idid, state
        let exception = item["amrException"]["exception"]
        // nullcode
        item["amrException"]["exception"] = exception.map((i, j) => {
            return i || item["amrException"]["exceptionCodes"][j] || ""
        }).sort()

        //   
        let ex = exception.join("");
        if (ex.includes("")) item["isParkingBarrier"] = true

        let warnId = item["stateId"] || 0;
        if (![7, 8, 10].includes(warnId)) {
            warnId = setWarnId(warnId, item["amrException"]);
        }
        item.warnId = warnId
        if ([7, 8].includes(warnId)) {
            dangerList.push(item);
        } else if ([4, 9, 6].includes(warnId)) {
            warningList.push(item);
        } else {
            normalList.push(item);
        }
    });
    // 
    if (!isSortAmr) {
        return result.sort((a, b) => idList.indexOf(a.id) - idList.indexOf(b.id))
    }
    // -- id
    let list = dangerList
        .sort((a, b) => a.id - b.id)
        .concat(warningList.sort((a, b) => a.id - b.id))
        .concat(normalList.sort((a, b) => a.id - b.id));
    idList = list.map(amr => amr.id)
    return list
}

// 
export const setWarnId = (warnId, amrException) => {
    let { level, exception } = amrException;
    if (exception.length > 0) {
        switch (level) {
            case 0: // 
                return 8;
            case 1: // 
                return 8;
            default:
                return warnId
        }
    } else {
        return warnId
    }
}