// 1.0
export const amrOldStatusEnum = (status)=>{
    switch (status) {
        case "CHARGING":
            return 3
        case "IDLE":
            return 1
        case "WAITING":
        case "CHARGE_RETRY":
        case "TRAFFIC_REQUIRE":
        case "REASSIGNING":
            return 8
        case "WARNING":
            return 4
        case "CONFIRMING":
            return 9
        default:
            return 2
    }
}