import store from '@/store'
export function addRouter(routerlist) {
  let routers = [] //
  let permissions = [] //
  // routerlist.splice(0,1)
  routerlist.forEach((items, index) => {
    let itemFirstObj = items[0].menuFirstPo  //
    let name = itemFirstObj.menuCode.slice(itemFirstObj.menuCode.lastIndexOf("/") + 1,) // 
    let component = 'Layout'
    // wcslayout
    if ((items[1] && items[1].menuSecondPo && items[1].menuSecondPo.menuCode.includes("submodule/zhonghui")) || (items[0] && items[0].menuSecondPo && items[0].menuSecondPo.menuCode.includes("submodule/zhonghui"))) {
      component = 'ZHLayout'
    }
    let namePath = `/${name}` // 
    let route = {
      path: index === 0 ? '/' : namePath,
      name,
      priority: itemFirstObj.priority,
      meta: {
        title: itemFirstObj.menuName,
        icon: name.replace(/\//g, "") ///iconiconfont.css
      },
      component: component,
      hidden: false
    } //
    let routeChildren = []  //
    if ((items[1] && items[1].menuSecondPo) || (items[0] && items[0].menuSecondPo)) {
      for (let i = 0; i < items.length; i++) {
        let item = items[i]
        permissions.push(item.permissionPo.permissionCode)
        if (item.menuSecondPo && item.menuSecondPo.menuCode === item.permissionPo.permissionCode) {
          let menuPo = item.permissionPo
          let childrenPath = menuPo.permissionCode.slice(menuPo.permissionCode.lastIndexOf("/") + 1,)
          let type = menuPo.permissionType
          let routeChild = {
            path: childrenPath,
            hidden: !(type === 0),
            meta: {
              keepAlive: false,
              title: menuPo.permissionName,
            },
            component: menuPo.permissionCode
          }
          routeChildren.push(routeChild)
        }
      }
    } else {
      for (let i = 0; i < items.length; i++) {
        let item = items[i]
        permissions.push(item.permissionPo.permissionCode)
      }
      let menuPo = items[0].permissionPo
      let childrenPath = "index"
      let type = menuPo.permissionType
      let routeChild = {
        path: childrenPath,
        hidden: !(type === 0),
        meta: {
          keepAlive: false,
          title: menuPo.permissionName,
        },
        component: menuPo.permissionCode + "/" + childrenPath
      }
      routeChildren.push(routeChild)
    }
    route.children = routeChildren
    routers.unshift(route)
  })
  routers.sort((a, b) => a.priority - b.priority)
  if (routers[0].name === 'zhonghui') {
    //  '/' wcslayout
    routers[1] = JSON.parse(JSON.stringify(routers[0]))
    routers[0].redirect = "/" + routers[0].name + "/" + routers[0].children[0].path
    routers[1].path = "/" + routers[0].name
  } else {
    routers[0].redirect = routers[0].children[0].component
    routers[0].children[0].path = routers[0].children[0].component
  }
  store.commit('setPermissions', permissions)  //vuex
  return routers
}
