/**
 * @param {string} path
 * @returns {Boolean}
 */
export function isExternal(path) {
  return /^(https?:|mailto:|tel:)/.test(path)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validUsername(str) {
  const valid_map = ['admin', 'editor']
  return valid_map.indexOf(str.trim()) >= 0
}

// form
export const numberRole = [
  { required: true, trigger: "blur", message: "" },
  {
    message: "",
    pattern: /^[-\d.]+$/,
    trigger: ["blur", "change"],
  },
]

// 
export const isFAKEPoint = (point) => {
  return /^FAKE|DOCK_FAKE|SWITCH_FAKE|ROTATE_FAKE|DOCK_FAKE|_COPY_FAKE|ROLLER_FAKE|INITIAL_OCCUPY_FAKE/.test(point)
}